import React, { useState, useEffect } from 'react';
import { Input } from '../../components/Interface/Input';
import { Label } from '../../components/Interface/Label';
import { Button } from '../../components/Interface/Button';
import { Switch } from '../../components/Interface/Switch';
import { wordPressService } from '../../services/wordpress';
import styles from './Settings.module.css';

const { ipcRenderer } = window.require('electron');
const Store = window.require('electron-store');
const store = new Store();

export default function Settings() {
  const [isTestingConnection, setIsTestingConnection] = useState(false);
  const [formData, setFormData] = useState({
    blogUrl: '',
    username: '',
    password: '',
    enableReminders: false,
    reminderTime: '09:00',
    keepOnTop: false
  });

  useEffect(() => {
    loadSettings();
  }, []);

  const loadSettings = () => {
    setFormData({
      blogUrl: store.get('blogUrl', ''),
      username: store.get('username', ''),
      password: '',
      enableReminders: store.get('enableReminders', false),
      reminderTime: store.get('reminderTime', '09:00'),
      keepOnTop: store.get('keepOnTop', false)
    });
  };

  const handleChange = (e) => {
    const { name, value, type, checked } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: type === 'checkbox' ? checked : value
    }));
  };

  const testConnection = async () => {
    try {
      setIsTestingConnection(true);
      await wordPressService.testConnection();
      console.log("Success: Successfully connected to WordPress!");
    } catch (error) {
      console.error("Error:", error.message);
    } finally {
      setIsTestingConnection(false);
    }
  };

  const handleSave = async () => {
    try {
      store.set('blogUrl', formData.blogUrl);
      store.set('username', formData.username);
      store.set('enableReminders', formData.enableReminders);
      store.set('reminderTime', formData.reminderTime);
      store.set('keepOnTop', formData.keepOnTop);

      // Update window behavior immediately
      await ipcRenderer.invoke('set-always-on-top', formData.keepOnTop);

      if (formData.password) {
        await ipcRenderer.invoke('save-credentials', {
          username: formData.username,
          password: formData.password
        });
        setFormData(prev => ({ ...prev, password: '' }));
      }

      console.log("Success: Settings saved successfully!");
    } catch (error) {
      console.error("Error saving settings:", error.message);
    }
  };

  return (
    <div className={styles.container}>
      <div className={styles.site}>
        <div className={styles.field}>
          <Label htmlFor="blogUrl" className={styles.label}>WordPress URL</Label>
          <Input
            type="url"
            id="blogUrl"
            name="blogUrl"
            value={formData.blogUrl}
            onChange={handleChange}
            placeholder="https://yourblog.com"
          />
        </div>

        <div className={styles.field}>
          <Label htmlFor="username" className={styles.label}>Username</Label>
          <Input
            type="text"
            id="username"
            name="username"
            value={formData.username}
            onChange={handleChange}
          />
        </div>

        <div className={styles.field}>
          <Label htmlFor="password" className={styles.label}>Application Password</Label>
          <Input
            type="password"
            id="password"
            name="password"
            value={formData.password}
            onChange={handleChange}
          />
        </div>

        <Button
          onClick={testConnection}
          disabled={isTestingConnection}
        >
          {isTestingConnection ? 'Testing...' : 'Test Connection'}
        </Button>
      </div>

      <div className={styles.switchField}>
        <Label htmlFor="keepOnTop" className={styles.switchLabel}>Keep window on top</Label>
        <Switch
          id="keepOnTop"
          name="keepOnTop"
          checked={formData.keepOnTop}
          onCheckedChange={(checked) => {
            setFormData(prev => ({ ...prev, keepOnTop: checked }));
            store.set('keepOnTop', checked);
            ipcRenderer.invoke('set-always-on-top', checked);
          }}
        />
      </div>

      {/* Footer */}
      <div className={styles.footer}>
        <Button onClick={handleSave}>
          Save Settings
        </Button>
      </div>
    </div>
  );
} 